import React, {useState} from 'react';
import {
  Alert,
  Dimensions,
  Linking,
  ScrollView,
  Share,
  StyleSheet,
  View,
} from 'react-native';
import {
  Button,
  Dialog,
  Divider,
  List,
  PaperProvider,
  Portal,
  Text,
  useTheme,
} from 'react-native-paper';
import {ISubscription, useAuthStore} from '@smarttestpro/smart-common';
import {AccountAnimation} from '../lotties';
import {
  AccountIcon,
  AppVersionIcon,
  ContactIcon,
  GenderIcon,
  LogoutIcon,
  SchoolIcon,
  WebsiteIcon,
} from '../icons';

const AccountScreen = () => {
  const {logout, user, defaultSubId, listSub, setDefaultSubId} = useAuthStore();
  const [visible, setVisible] = React.useState(false);
  const [expanded, setExpanded] = useState(false);

  const theme = useTheme();

  const handlePress = () => setExpanded(!expanded);

  const handleItemSelect = (item: string) => {
    setDefaultSubId(item);
    setExpanded(false); // Collapse the list after selecting an item
  };

  const showDialog = () => setVisible(true);

  const hideDialog = () => setVisible(false);

  const onShare = async () => {
    try {
      const result = await Share.share({
        message:
          'Tải ứng dụng SmartTest cho iOS/Android tại https://smarttest.pro/app',
      });
      if (result.action === Share.sharedAction) {
        if (result.activityType) {
          // shared with activity type of result.activityType
        } else {
          // shared
        }
      } else if (result.action === Share.dismissedAction) {
        // dismissed
      }
    } catch (error: any) {
      Alert.alert('Có lỗi xảy ra vui lòng thử lại!');
    }
  };

  return (
    <PaperProvider>
      <ScrollView style={{backgroundColor: 'white'}}>
        <View style={styles.container}>
          <View
            style={{
              height: 200,
              width: Dimensions.get('screen').width,
              justifyContent: 'center',
              alignItems: 'center',
              backgroundColor: theme.colors.primary,
              display: 'flex',
            }}>
            <AccountAnimation />
            <Text style={{fontSize: 18, color: 'white', marginTop: -16}}>
              @{user?.preferred_username}
            </Text>
            <Text style={{fontSize: 14, color: 'white', marginTop: 4}}>
              {user?.email}
            </Text>
          </View>
          <View style={{paddingHorizontal: 8}}>
            <List.Section title="Thông tin cá nhân">
              <List.Item
                title={user?.name}
                left={() => (
                  <View style={styles.itemV3} pointerEvents="box-none">
                    <AccountIcon width={24} height={24} />
                  </View>
                )}
              />
              <Divider bold />
              <List.Item
                //title={defaultSubId}
                left={() => (
                  <View style={styles.itemV3} pointerEvents="box-none">
                    <SchoolIcon width={24} height={24} />
                  </View>
                )}
                title={() => (
                  <View>
                    <List.Accordion
                      title={
                        listSub.filter(item =>
                          item.subId.includes(defaultSubId || '0'),
                        )[0].subNm
                      }
                      expanded={expanded}
                      onPress={handlePress}>
                      {listSub.map((item: ISubscription) => (
                        <List.Item
                          key={item.subId}
                          title={item.subNm}
                          onPress={() => handleItemSelect(item.subId)}
                        />
                      ))}
                    </List.Accordion>
                  </View>
                )}
              />

              {/* <Divider bold />
              <List.Item
                title=" "
                left={() => (
                  <View style={styles.itemV3} pointerEvents="box-none">
                    <GenderIcon width={24} height={24} />
                  </View>
                )}
              />
              <Divider bold />
              <List.Item
                title=" "
                description=" "
                left={() => (
                  <View style={styles.itemV3} pointerEvents="box-none">
                    <SchoolIcon width={24} height={24} />
                  </View>
                )}
              /> */}
            </List.Section>
            <Divider bold />

            <List.Section title="Thông tin ứng dụng">
              <List.Item
                title="Phiên bản ứng dụng"
                description="SmartTestPro@v0.0.1"
                left={() => (
                  <View style={styles.itemV3} pointerEvents="box-none">
                    <AppVersionIcon color="#000" width={24} height={24} />
                  </View>
                )}
              />
              <Divider />
              <List.Item
                title="https://smarttest.pro"
                onPress={() => {
                  Linking.openURL('https://smarttest.pro');
                }}
                left={() => (
                  <View style={styles.itemV3} pointerEvents="box-none">
                    <WebsiteIcon color="#000" width={24} height={24} />
                  </View>
                )}
              />
              {/* <Divider />
              <List.Item
                title="Liên hệ"
                onPress={() => {
                  Linking.openURL('tel:0868686868');
                }}
                description="---------"
                left={() => (
                  <View style={styles.itemV3} pointerEvents="box-none">
                    <ContactIcon width={24} height={24} />
                  </View>
                )}
              /> */}
              <Divider />
              <List.Item
                title="Chia sẻ ứng dụng"
                onPress={onShare}
                left={props => <List.Icon {...props} icon="share-outline" />}
                // left={() => (
                //   <View style={styles.itemV3} pointerEvents="box-none">
                //     <ContactIcon width={24} height={24} />
                //   </View>
                // )}
              />
              <Divider />
            </List.Section>
            {/* <Text>{JSON.stringify(user)}</Text> */}
            <View style={{alignItems: 'center'}}>
              <Button
                style={{marginTop: 12, marginBottom: 12}}
                mode="outlined"
                icon={() => (
                  <View style={{width: 24, height: 24}}>
                    <LogoutIcon width={24} height={24} />
                  </View>
                )}
                textColor="black"
                buttonColor="white"
                contentStyle={{flexDirection: 'row-reverse'}}
                onPress={() => showDialog()}>
                Đăng xuất
              </Button>
              <Portal>
                <Dialog visible={visible} onDismiss={hideDialog}>
                  <Dialog.Title>Thông báo</Dialog.Title>
                  <Dialog.Content>
                    <Text variant="bodyMedium">
                      Bạn có chắc chắn muốn đăng xuất khỏi tài khoản?
                    </Text>
                  </Dialog.Content>
                  <Dialog.Actions>
                    <Button
                      onPress={() => {
                        hideDialog();
                      }}>
                      Ở lại
                    </Button>
                    <Button
                      onPress={() => {
                        hideDialog();
                        logout();
                      }}>
                      Đăng xuất
                    </Button>
                  </Dialog.Actions>
                </Dialog>
              </Portal>
            </View>
          </View>
        </View>
      </ScrollView>
    </PaperProvider>
  );
};

const styles = StyleSheet.create({
  container: {
    backgroundColor: '#fff',
    flex: 1,
  },

  itemV3: {
    paddingLeft: 16,
    alignItems: 'center',
    justifyContent: 'center',
  },
});

export default AccountScreen;
